<template>
  <div>
    <div class="column column-8 column-centered-m single-project-wrapper">
        <div class="row">
          <div class="column column-12">
            <h4>{{project.name}}</h4>
          </div>
          
          <div class="column column-12">
            <button id="show-modal" @click="showModal = true">Show Modal</button>

              <modal :title="this.project.name" :description="this.project.description" :stack="this.project.stack" :images="this.project.images" v-if="showModal" @close="showModal = false">
              
              </modal>

          </div>
        </div>
    </div>
    <div class="clearfix"></div>
  </div>
</template>

<script>

  import Modal from './ProjectModal.vue'

  export default {
    name: 'singleProject',
    props: {
      project: {}
    },
    data: function () {
      return {
        showModal: false
      }
    },
    components: {
      modal: Modal
    },
    methods: {
      showModal: function () {
        this.showModal = true
      }
    }
  }
</script>

<style scoped>
  .single-project-wrapper 
  {
    padding: 2em 1em;
    box-sizing: border-box;
    box-shadow: 0px 1px 10px 0px rgba(0,0,0,0.15);
  }
</style>



