<template>
    <div class="project-list-wrapper">
    
        <div class="row-100">
          <projectSingle  v-for="project in projects" :key="project.id" :project="project"></projectSingle>
        </div>
    
    </div>
</template>

<script>
  import ProjectSingle from './ProjectSingle.vue'

  let projectData = [
    {
      id: 1,
      name: 'Derby Croatia',
      description: 'Derby Croatia is a international competition of young racing pigeons that takes place in Croatia. Races vary from 100km to 400km and it gathers competitors from various parts of Europe.',
      stack: [
        'C#',
        '.NET MVC',
        'Javascript'
      ],
      images: [
        'http://danijelhrcek.com/img/projectImages/derbyCroatia/derbyCroatiaSmall.jpg',
        'http://danijelhrcek.com/img/projectImages/dsQuiz/dsQuizSmall.jpg'
      ]
    },
    {
      id: 2,
      name: 'DSQuiz',
      description: 'This is a .NET MVC app with entity framework for knowledge evaluation. Logic behind this app is quite simple. You can create different categories ( programming skill, knowledge test, intelligence test etc. ). Each quiz you create can belong to any of the categories. You can create new questions with answers and then assign them to the specific quiz, or remove them. App also features user registration( you can also use google login ). Once you have logged in, you can see your results or take any of the quizzes. Privilege of creating, editing or deleting app data has only app admin.',
      stack: [
        'C#',
        '.NET MVC',
        'WebPack'
      ],
      images: [
        'http://danijelhrcek.com/img/projectImages/derbyCroatia/derbyCroatiaSmall.jpg',
        'http://danijelhrcek.com/img/projectImages/dsQuiz/dsQuizSmall.jpg'
      ]
    }
  ]

  export default {
    name: 'projectList',
    data: function () {
      return {
        projects: []
      }
    },
    created: function () {
      this.projects = projectData
    },
    components: {
      projectSingle: ProjectSingle
    }
  }
</script>

<style scoped>
  .project-list-wrapper {}
</style>



