<template>
    <div>
        <div class="modal-mask">
            <div class="modal-wrapper">
                <div class="modal-container">

                    <div class="modal-header">
                        <slot name="header">
                        <h3> {{ title }} </h3>
                        </slot>
                        <a class="modal-default-button" @click="$emit('close')">
                            Close
                        </a>
                        <div class="clearfix"></div>
                        <hr class="hr-project">
                        <ul class="content-skills">
                            <li v-for="technology in stack">
                                {{ technology }}
                            </li>
                        </ul>
                    </div>
                    
                    <div class="modal-body">
                        <slot name="body">
                        {{ description }}
                        </slot>
                    </div>

                    <div class="modal-images">
                        <carousel3d :controls-visible="true" :clickable="true">
                            <slide v-for="(image, i) in images" :index="i" :key="i">
                                <img v-bind:src="image">
                            </slide>
                        </carousel3d>
                    </div>

                    <div class="modal-footer">
                        <a v-bind:href="githubUri" class="btn-footer">Github</a>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>

    import { Carousel3d, Slide } from 'vue-carousel-3d'

    export default {
        name: 'modal',
        components: {
            slide: Slide,
            carousel3d: Carousel3d
        },
        props: {
            title: '',
            description: '',
            githubUri: '',
            stack: {
                type: Array,
                required: false
            },
            images: {
                type: Array,
                required: false
            }
        },
        data: function () {
            return {
                slides: 7
            }
        }
    }
</script>

<style lang="scss">

     @import "../../assets/settings.scss";

    .modal-mask {
        position: fixed;
        z-index: 9998;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(0, 0, 0, .5);
        display: table;
        transition: opacity .3s ease;
    }

    .modal-wrapper {
        display: table-cell;
        vertical-align: middle;
    }

    .modal-container {
        width: 70%;
        margin: 0px auto;
        padding: 20px 30px;
        background-color: #fff;
        border-radius: 2px;
        box-shadow: 0 2px 8px rgba(0, 0, 0, .33);
        transition: all .3s ease;
    }

    .modal-header h3 {
        float: left;
        margin: 0;
        color: $primary-prime;
        text-align: left;
    }

    .hr-project{
        background-color: rgba(0,0,0,0.2);
    }

    .modal-body {
        margin: 20px 0;
    }

    .btn-footer{
        padding: 5px 10px;
        color: white;
        background-color: rgba(65,131,196,0.9);
        width: 100%;

        transition: all 0.2s;
    }

    .btn-footer:hover{
        background-color: #fff;
        color: black;
        cursor: pointer;

        box-shadow: 1px 1px 5px rgba(0,0,0,0.2);
    }

    .modal-default-button {
        float: right;
        margin: 5px;
        padding: 5px 10px;
        background-color: #efefef;
        border: 0;

        transition: all 0.2s;
    }

    .modal-default-button:hover{
        cursor: pointer;
        box-shadow: 1px 1px 5px rgba(0,0,0,0.2);
    }

    .content-skills {
        font-size: 12px;
        padding: 0;
        margin-bottom: 0;
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
    }

    .content-skills li {
        background: #40484D;
        border-radius: 2px;
        display: inline-block;
        padding: 6px 10px;
        color: rgba(255, 255, 255, 0.7);
        margin: 3px 2px;
        text-align: center;
        flex-grow: 1;

        box-shadow: 1px 1px 5px rgba(0,0,0,0.2);
    }

</style>



